html {
  scroll-behavior: smooth;
}

.container {
  font-family: noto-sans-cjk-kr,sans-serif;
  font-weight: 400;
  font-style: normal;
}

.banner {
  width: 100%;
  height: 640px;
  background-color: #f8f8f8;
  display: flex;
  justify-content: center;
  align-items: center;
}

.banner_img {
  margin: 0 auto;
}

.rank {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.recRegi {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f8f8f8;
}

.rankTitle  {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 120px;
  margin-bottom: 80px;
}

.regiTitle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 120px;
  margin-bottom: 80px;
}

.title_txt {
  font-family: noto-sans-cjk-kr,sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 24px;
}

.sub_title {
  margin: 16px;
  font-size: 16px;
  color: #999999;
}

.Content {
  width: 1440px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 200px;
}

.rankImgBox {
  display: flex;
  justify-content: center;
  align-items: flex-end;
  width: 300px;
  height: 320px;
  background-color: #f8f8f8;
  transition: all 0.2s linear;
}

.rankSub {
  width: 100%;
  height: 80px;
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
}

.rankSubTitle {
  margin-top: 20px;
}

#tag {
  font-size: 14px;
  color: rgb(153, 153, 153);
}

.rankTag {
  display: flex;
  justify-content: flex-start;
}

.rankImgBox span {
  margin: 16px;
}

.rankImgBox {
  cursor: pointer;
}

.rankImgBox:hover {
  transform: scale(1.2, 1.2);
}

.recContent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.recBox {
  /* width: 1200px; */
  width: 1440px;
  height: 360px;
  display: flex;
  justify-content: space-around;
  justify-items: center;
  margin-bottom: 80px;
}

.recImgBox {
  width: 540px;
  height: 360px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ffffff;
  /* margin: 0 80px; */
  margin-bottom: 80px;
}

.recSubContent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 480px;
  height: 100%;
}

.recImgBox span {
  margin: 0 auto;
}

#recName {
  font-family: noto-sans-cjk-kr,sans-serif;
  font-weight: 500;
  font-style: normal;
  font-size: 24px;
  margin-bottom: 16px;
}

#recText {
  font-size: 16px;
  color: rgb(153, 153, 153);
}

/* .searchBtn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
} */

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 40px;
  border: 1px solid #999999;
  background-color: #ffffff;
  border-radius: 30px;
  margin-bottom: 100px;
  cursor: pointer;
}

#btn span {
  margin: 8px;
}

/* .btn:hover {
  border: 1px solid rgb(50, 220, 199);
  color: rgb(50, 220, 199);
} */

#arrow {
  color: #999999;
}

.keyWordSearch {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.keyTitle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 120px;
  margin-bottom: 20px;
}

.keyword {
  width: 720px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}

.keyAll {
  width: auto;
  height: 30px;
  border-radius: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
  border: 1px solid rgb(50, 220, 199);
  cursor: pointer;
}

.keyAll span {
  color: rgb(50, 220, 199);
  margin: 16px;
}

#keyValue {
  width: auto;
  height: 30px;
  border: 1px solid #999999;
  border-radius: 30px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
  cursor: pointer;
}

#keyValue span {
  color: rgb(153, 153, 153);
  margin: 16px;
}

.searchBox {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  margin-bottom: 100px;
  position: relative;  
}

#searchIcon {
  color: rgb(153, 153, 153);
  position: absolute;
  left: 40px;
}

#inputKey {
  width: 720px;
  padding: 16px 60px;
  border: 1px solid #999999;
  border-radius: 30px;
  outline: none;
  margin-left: 10px;
  font-size: 16px;
}

#inputKey:focus {
  border: 1px solid rgb(50, 220, 199);
}

input:-webkit-autofill {
  box-shadow: 0 0 0 1000px #fff inset;
}