.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: noto-sans-cjk-kr,sans-serif;
  font-weight: 400;
  font-style: normal;
}

.createTitle {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 80px 0;
}

#title {
  font-size: 24px;
  font-weight: 500;
}

#subTitle {
  font-size: 16px;
  font-weight: 400;
  color: #999999;
  margin: 16px 0;
}

.dietBox {
  width: 1080px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: 60px 0;
}

.dietTitle {
  display: flex;
  flex-direction: column;
  margin-top: 32px;
}

#time {
  font-size: 24px;
  font-weight: 500;
  margin: 8px 0;
}

#menuTitle {
  font-size: 20px;
}

#line {
  display: flex;
  justify-content: center;
  align-items: center;
  border-left: 2px solid rgb(50, 220, 199);
  margin: 16px;
}

#comment {
  font-size: 16px;
  margin-left: 8px;
  color: #999999;
}

.menuImg {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 300px;
  height: 300px;
  border-radius: 30px;
  background-color: #f8f8f8;
  cursor: pointer;
}

.dietInfoBox {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 1080px;
  margin: 60px 0;
}

.dietInfoBox span {
  font-size: 20px;
  font-weight: 500;
}

.dietName {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 20px 0;
}

#dietname {
  width: 240px;
  margin: 16px;
  border: none;
  border-bottom: 1px solid #999999;
  font-size: 16px;
}

#dietname:focus {
  border-bottom: 1px solid rgb(50, 220, 199);
}

input:focus, textarea:focus {
  outline: none;
  border: 1px solid rgb(50, 220, 199);
}

input:-webkit-autofill, textarea:-webkit-autofill{
  box-shadow: 0 0 0 1000px #fff inset;
}

textarea {
  resize: none;
  border: 1px solid #999999;
}

.dietComment {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 20px 0;
  /* align-items: flex-start; */
}

#dietcomment {
  width: 100%;
  height: 240px;
  margin: 16px 0;
  font-size: 16px;
}

.dietKeyword {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin: 20px 0;
}

.addKeyword {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin: 20px 0;
}

.keyText {
  width: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.keyText i {
  position: relative;
  color: #DB403D;
  cursor: pointer;
}

#setKey {
  width: 20px;
  height: 30px;
  border: 1px solid #999999;
  border-radius: 30px;
  padding-left: 16px;
  font-size: 14px;
}

#setKey:focus {
  border: 1px solid rgb(50, 220, 199);
}

.addBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;
  border: 1px solid #999999;
  border-radius: 100%;
  cursor: pointer;
  margin: 0 16px;
}

.addBtn i {
  color: rgb(50, 220, 199);
}

#dietkeyword {
  width: 100%;
  height: 40px;
  margin: 16px 0;
  font-size: 16px;
}

.regiBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 60px;
}

#regibtn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 40px;
  border: 1px solid #999999;
  border-radius: 30px;
  cursor: pointer;
}

#regibtn span {
  font-size: 16px;
  font-weight: 400;
}

/* #regibtn:hover {
  border: 1px solid rgb(50, 220, 199);
  color: rgb(50, 220, 199);
} */

#arrow {
  margin-left: 16px;
  color: rgb(50, 220, 199);
}

@media screen and (max-width: 992px) {
  .dietBox {
    width: 800px;
  }
  .dietInfoBox {
    width: 800px;
  }
}

